@font-face {
  font-display: swap;
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
}
@font-face {
  font-display: swap;
  font-style: normal;
  font-weight: 500;
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
}
@font-face {
  font-display: swap;
  font-style: normal;
  font-weight: 600;
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
}
@font-face {
  font-display: swap;
  font-style: normal;
  font-weight: 700;
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #011c40;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f9fbfd;
}

a {
  color: #011c40;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #f9fbfd;
  -webkit-text-fill-color: #011c40;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1160px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.btn {
  display: inline-flex;
  font-size: 14px;
  font-weight: 600;
  font-family: "Montserrat", "Arial", sans-serif;
  border: none;
  color: #011c40;
  background-color: #f9fbfd;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.custom-input {
  position: relative;
  display: flex;
  width: 303px;
  padding: 10px 0 10px;
}
@media screen and (max-width: 767px) {
  .custom-input {
    padding: 10px 0 10px;
    width: 100%;
  }
}
.custom-input label {
  display: flex;
  align-items: center;
  width: 100%;
}
@media screen and (max-width: 767px) {
  .custom-input label {
    align-items: flex-start;
    flex-direction: column;
  }
}
.custom-input label input,
.custom-input label textarea {
  flex-grow: 1;
  max-width: 300px;
  min-height: 43px;
  padding: 10px;
  border: none;
  outline: none;
  transition: border 0.3s ease;
}
@media screen and (max-width: 767px) {
  .custom-input label input,
.custom-input label textarea {
    max-width: unset;
    width: 100%;
  }
}
.custom-input label input:hover,
.custom-input label textarea:hover {
  border: 1px solid rgba(1, 28, 64, 0.5);
}
.custom-input label input:focus,
.custom-input label textarea:focus {
  border: 1px solid rgba(1, 28, 64, 0.8);
}
@media screen and (max-width: 767px) {
  .custom-input__label {
    margin: 0 0 18px 0;
  }
}
.custom-input__error {
  position: absolute;
  right: 0;
  bottom: 35px;
  color: #ff1553;
  opacity: 0;
  transition: opacity 0.3s ease;
}
@media screen and (max-width: 767px) {
  .custom-input__error {
    bottom: 20px;
  }
}
.custom-input.is-invalid input,
.custom-input.is-invalid textarea {
  border: 2px solid #ff1553;
}
.custom-input.is-invalid input:focus, .custom-input.is-invalid input:hover,
.custom-input.is-invalid textarea:focus,
.custom-input.is-invalid textarea:hover {
  border: 2px solid rgba(255, 21, 83, 0.5);
}
.custom-input.is-valid input {
  border: 2px solid #22b259;
}
.custom-input.is-valid input:focus, .custom-input.is-valid input:hover {
  border: 2px solid rgba(34, 178, 89, 0.5);
}
.custom-input .input-message {
  position: absolute;
  left: 0;
  bottom: -10px;
}
.custom-input .input-message.is-invalid {
  color: #ff0000;
}
.custom-input .input-message.is-valid {
  color: #15ab07;
}

.custom-toggle {
  position: relative;
  font-size: 10px;
  line-height: 1.3;
  font-weight: 400;
  width: 100%;
  display: flex;
}
@media screen and (max-width: 767px) {
  .custom-toggle {
    width: 100%;
  }
}
.custom-toggle input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0);
}
.custom-toggle label {
  position: relative;
  padding-top: 20px;
  padding-bottom: 30px;
  padding-left: 24px;
  cursor: pointer;
}
.custom-toggle label span {
  margin-bottom: 0;
  margin-left: 0;
}
@media (max-width: 767px) {
  .custom-toggle label {
    padding-top: 13px;
    padding-left: 35px;
  }
}
.custom-toggle--checkbox .custom-toggle__icon {
  position: absolute;
  top: 15px;
  left: 0;
  width: 20px;
  height: 20px;
  background-color: #f9fbfd;
  transition: border 0.3s ease;
}
.custom-toggle--checkbox .custom-toggle__icon::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  background-color: rgba(44, 57, 242, 0);
  transform: translate(-50%, -50%);
  transition: background-color 0.3s ease;
}
.custom-toggle input:checked + .custom-toggle__icon::before {
  content: "";
  width: 12px;
  height: 8px;
  background-image: url("../img/svg/icon-checkbox.svg");
  background-repeat: no-repeat;
}
.custom-toggle.is-invalid label span {
  color: #ff1553;
}
.custom-toggle .input-message {
  position: absolute;
  left: 0;
  bottom: 10px;
}
.custom-toggle .input-message.is-invalid {
  color: #ff0000;
}
.custom-toggle .input-message.is-valid {
  color: #15ab07;
}

.header {
  background-image: url("../img/images/main-bg-desktop.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 35px;
  position: relative;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .header {
    background-image: url("../img/images/main-bg-desktop@2x.jpg");
  }
}
@media (max-width: 1023px) {
  .header {
    background-image: url("../img/images/main-bg-tablet.jpg");
  }
}
@media (max-width: 1023px) and (min-resolution: 144dpi), (max-width: 1023px) and (min-resolution: 1.5dppx) {
  .header {
    background-image: url("../img/images/main-bg-tablet@2x.jpg");
  }
}
@media (max-width: 767px) {
  .header {
    background-image: url("../img/images/main-bg-mobile.jpg");
  }
}
@media (max-width: 767px) and (min-resolution: 144dpi), (max-width: 767px) and (min-resolution: 1.5dppx) {
  .header {
    background-image: url("../img/images/main-bg-mobile@2x.jpg");
  }
}

.header__user-navigation {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
}
.header__user-navigation--opened {
  width: 85%;
  align-content: flex-start;
  background-color: #f9fbfd;
  box-shadow: -500px 500px 21px 500px rgba(0, 43, 102, 0.5);
}
.header__user-navigation--closed {
  width: 100%;
  min-height: 40px;
}
@media (max-width: 767px) {
  .header__user-navigation {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    position: absolute;
    right: -1px;
    top: 0;
    min-height: 570px;
    overflow: hidden;
  }
}

@media (max-width: 767px) {
  .header__user-navigation .user-navigation__list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-top: 75px;
    padding-left: 16px;
    padding-right: 29px;
    padding-bottom: 20px;
  }
}

.header__content {
  padding-top: 286px;
  padding-left: 178px;
  padding-bottom: 286px;
}
@media (max-width: 1023px) {
  .header__content {
    padding-left: 0;
  }
}
@media (max-width: 767px) {
  .header__content {
    padding-top: 185px;
    padding-bottom: 185px;
  }
}

.header__content h1 {
  font-weight: 700;
  color: #f9fbfd;
  font-size: 54px;
  line-height: 1;
  text-shadow: #011c40 0 2px 5px;
}
@media (max-width: 1023px) {
  .header__content h1 {
    font-size: 50px;
    text-shadow: none;
  }
}
@media (max-width: 767px) {
  .header__content h1 {
    font-size: 36px;
    line-height: 1.25;
  }
}

.header__content span {
  color: #f9fbfd;
  font-size: 20px;
  text-shadow: #011c40 0 2px 5px;
}
@media (max-width: 1023px) {
  .header__content span {
    font-size: 18px;
  }
}
@media (max-width: 767px) {
  .header__content span {
    font-size: 16px;
  }
}

.logotype a {
  display: block;
  mask-image: url("../img/svg/logotype.svg");
  width: 70px;
  height: 38px;
  background-color: #f9fbfd;
}
@media (hover: hover) {
  .logotype a:hover:not(.focus-visible) {
    opacity: 0.5;
  }
}
@media (hover: hover) {
  .logotype a:hover:not(.focus-visible) {
    opacity: 0.5;
  }
}
.logotype a.focus-visible:focus {
  opacity: 0.5;
}
@media (max-width: 1023px) {
  .logotype a {
    mask-image: url("../img/svg/logotype-tablet.svg");
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .logotype a {
    mask-image: url("../img/svg/logotype-mobile.svg");
    width: 42px;
    height: 22px;
  }
}

.user-navigation__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: grid;
  max-width: 600px;
  grid-template-columns: repeat(4, auto);
  align-content: center;
  column-gap: 33px;
  row-gap: 10px;
  align-self: center;
}
@media (max-width: 1023px) {
  .user-navigation__list {
    align-self: flex-start;
  }
}

.user-navigation a {
  color: #f9fbfd;
  font-weight: 500;
}
@media (hover: hover) {
  .user-navigation a:hover:not(.focus-visible) {
    opacity: 0.5;
  }
}
@media (hover: hover) {
  .user-navigation a:hover:not(.focus-visible) {
    opacity: 0.5;
  }
}
.user-navigation a.focus-visible:focus {
  opacity: 0.5;
}
@media (max-width: 1023px) {
  .user-navigation a {
    font-size: 14px;
  }
}
@media (max-width: 767px) {
  .user-navigation a {
    color: #011c40;
  }
}

.user-navigation__toggle {
  display: none;
}
@media (max-width: 767px) {
  .user-navigation__toggle {
    padding: 0;
    background: none;
    border: none;
    display: block;
    position: absolute;
    right: 14px;
    top: 20px;
  }
}
@media (hover: hover) {
  .user-navigation__toggle:hover:not(.focus-visible) {
    opacity: 0.5;
  }
}
.user-navigation__toggle.focus-visible:focus {
  opacity: 0.5;
}

@media (max-width: 767px) {
  .header__user-navigation--opened .header__logotype a {
    background-color: #011c40;
    position: absolute;
    left: 16px;
    top: 20px;
  }
  .header__user-navigation--opened .user-navigation__toggle {
    width: 13px;
    height: 13px;
  }
  .header__user-navigation--opened .user-navigation__toggle::after,
.header__user-navigation--opened .user-navigation__toggle::before {
    content: "";
    position: absolute;
    height: 2px;
    width: 13px;
    left: 0;
    top: 40%;
    background-color: #011c40;
    transform: rotate(45deg);
  }
  .header__user-navigation--opened .user-navigation__toggle::before {
    transform: rotate(-45deg);
  }
  .header__user-navigation--closed .user-navigation__list {
    display: none;
  }
  .header__user-navigation--closed .header__logotype a {
    position: absolute;
    left: 16px;
    top: 20px;
  }
  .header__user-navigation--closed .user-navigation__toggle {
    width: 24px;
    height: 12px;
  }
  .header__user-navigation--closed .user-navigation__toggle::after {
    content: "";
    position: absolute;
    width: 24px;
    height: 2px;
    right: 0;
    top: 0;
    background-color: #f9fbfd;
    box-shadow: 0 5px 0 0 #f9fbfd, 0 10px 0 0 #f9fbfd;
  }
}
.footer {
  box-sizing: border-box;
  background-color: #011c40;
  padding-top: 40px;
  padding-bottom: 40px;
}
@media (max-width: 767px) {
  .footer {
    padding-top: 30px;
    padding-bottom: 30px;
  }
