.footer .container {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}
@media (max-width: 767px) {
  .footer .container {
    flex-direction: column;
    align-items: flex-start;
  }
}

.footer__user-navigation {
  display: flex;
}
@media (max-width: 767px) {
  .footer__user-navigation {
